.wrapper {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(45deg, #2db0e0 0%, #ffffcc 100%);
}

.main {
  min-height: 55vh;
  background: #b0dfcf;
  box-shadow: 0px 2px 80px -20px rgba(0,0,0,0.5);
  width: 55%;
  margin: 0 auto;
}

.info {
  min-height: 55vh;
  background: url("img/polygon.jpg") center center no-repeat;
  background-blend-mode: multiply;
  background-color: #909090;
  border: 2px solid #005777;
  border-right: 2px solid #005777;
  background-size: cover;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  text-align: center;
  color: #000;
}

.form {
  min-height: 55vh;
  padding-top: 100px;
  padding-left: 50px;
  border-top: 2px solid #005777;
  border-bottom: 2px solid #005777;
  border-right: 2px solid #005777;
}

input[type="text"] {
  background-color: transparent;
  border: 0;
  border-bottom: solid 2px #005777;
  width: 70%;
  padding-bottom: 4px;
  color:#005777;
  font-weight: lighter;
  margin-bottom: 30px;
  margin-right: 25px;
  font-size: 25px;
  outline: none;
}

input::-webkit-input-placeholder {
  color: #005777;
}

input::-moz-placeholder {
  color:#005777;
}

button {
  border: 2px solid#005777;
  border-bottom: 4px solid#005777;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 2px;
  cursor: pointer;
  background-color: #b0dfcf;
  color: #005777;
  outline: none!important;
}

button:disabled {
  cursor: not-allowed;
}

button:hover {
  border-bottom: 2px solid#005777;
  position: relative;
  top: 2px;
}

button:active {
  border-bottom: 2px solid transparent;
  border-color: transparent;
  position: relative;
  background-color: #005777;
  color: #b0dfcf;
  top: 2px;
}

.info h2 {
  margin-top: 205px;
  font-size: 60px;
  color: #b0dfcf;
}

.info p {
  color: #b0dfcf;
  font-weight: bold;
}

.infoWeath {
  width: 100%;
  font-size: 30px;
  color: #005777;
}

.error {
  color: #005777;
  font-weight: bold;
  font-size: 25px;
}